#lock { 
    font-size: 48px;
    text-align: center;
    margin: auto;
    border: 1px solid black;
    padding: 20px;
    border-radius: 10px;
    background-color: #333;
    color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    animation: fadeIn 2s ease-in-out, scaleUp 2s ease-in-out;
}

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes scaleUp {
    0% { transform: scale(0.5); }
    100% { transform: scale(1); }
}
body {
    display: flex;
    justify-content: center;   
    align-items: center;       
    height: 100vh;      
  }
  